<template>
  <div class="pageContent">
    <el-card class="filter-container" shadow="never">
      <div class="flex-row-between">
        <div>对账日期：{{$route.query.balanceDay}}</div>
        <div>本期收入金额：{{$route.query.in}}</div>
        <div>本期支出金额：{{$route.query.out}}</div>
        <div>余额：{{shopAsset}}</div>
      </div>
    </el-card>
    <el-card class="filter-container mt10" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="支付方向：">
            <el-select v-model="searchList.amountType" clearable>
              <el-option label="收入" value="1"></el-option>
              <el-option label="支出" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务单号：">
            <el-input style="width: 203px" v-model="searchList.businessNumber" clearable @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="支付单号/流水号：">
            <el-input style="width: 203px" v-model="searchList.wxNumber" clearable @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="单据类型：">
            <el-select v-model="searchList.tradeType" clearable>
              <el-option label="订单入账" value="1"></el-option>
              <el-option label="退款" value="24"></el-option>
              <el-option label="交易手续费" value="21"></el-option>
              <el-option label="交易手续费退回" value="2"></el-option>
              <el-option label="佣金" value="22"></el-option>
              <el-option label="佣金退回" value="4"></el-option>
              <el-option label="邀请奖励" value="26"></el-option>
              <el-option label="邀请奖励退回" value="5"></el-option>
              <el-option label="提现" value="25"></el-option>
              <el-option label="提现手续费" value="23"></el-option>
              <el-option label="充值" value="3"></el-option>
              <el-option label="会员卡收入" value="6"></el-option>

            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container mt10" shadow="never">
      <div class="flex-row-start" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
        <div class="succeseFont" style="margin-left:20px;">对账单统计日期以账单明细中入账时间为准，如需下单时间维度的汇总，可在账单明细下载相应报表</div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card style="margin-top:20px;" class="filter-container" shadow="never">
      <el-table ref="multipleTable" :data="list" tooltip-effect="dark" style="width: 100%">
        <el-table-column prop="businessNumber" width="200">
          <template slot="header" slot-scope="scope">
            业务单编号
            <el-tooltip :data-type="scope.row" class="item" effect="dark" content="业务单号，指的是不同的业务的单号，例如：订单是订单号，提现是提现单号，分润是分润单号。关联单号，是指相关的订单号" placement="top">
              <i class="el-icon-question"></i>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column prop="wxNumber" label="支付单号/流水号" width="200">
          <template slot-scope="scope">
            <div>{{scope.row.wxNumber ? scope.row.wxNumber : '-'}}</div>
          </template>
        </el-table-column>
        <el-table-column label="单据类型/交易类型" width="200">
          <template slot-scope="scope">
            <div v-if="scope.row.tradeType == 1">订单入账</div>
            <div v-if="scope.row.tradeType == 2">交易手续费退回</div>
            <div v-if="scope.row.tradeType == 3">充值</div>
            <div v-if="scope.row.tradeType == 4">佣金退回</div>
            <div v-if="scope.row.tradeType == 5">邀请奖励退回</div>
            <div v-if="scope.row.tradeType == 21">交易手续费</div>
            <div v-if="scope.row.tradeType == 22">佣金</div>
            <div v-if="scope.row.tradeType == 23">提现手续费</div>
            <div v-if="scope.row.tradeType == 24">交易退款</div>
            <div v-if="scope.row.tradeType == 25">提现</div>
            <div v-if="scope.row.tradeType == 26">邀请奖励</div>
          </template>
        </el-table-column>
        <el-table-column prop="description" label="名称" width="400"></el-table-column>
        <el-table-column prop="amount" label="账单金额" width="200"></el-table-column>
        <!--  -->
        <el-table-column prop="amount" label="收支类型" width="200">
          <template slot-scope="scope">
            <div v-if="scope.row.tradeType == 1">收入</div>
            <div v-if="scope.row.tradeType == 2">收入</div>
            <div v-if="scope.row.tradeType == 3">收入</div>
            <div v-if="scope.row.tradeType == 4">收入</div>
            <div v-if="scope.row.tradeType == 5">收入</div>
            <div v-if="scope.row.tradeType == 21">支出</div>
            <div v-if="scope.row.tradeType == 22">支出</div>
            <div v-if="scope.row.tradeType == 23">支出</div>
            <div v-if="scope.row.tradeType == 24">支出</div>
            <div v-if="scope.row.tradeType == 25">支出</div>
            <div v-if="scope.row.tradeType == 26">支出</div>
          </template>
        </el-table-column>
        <el-table-column label="支付方式" width="200">
          <template slot-scope="scope">
            <div v-if="scope.row.payType == 0">微信代销</div>
            <div v-if="scope.row.payType == 1">微信自有</div>
            <div v-if="scope.row.payType == 2">小程序支付</div>
          </template>
        </el-table-column>
        <el-table-column label="结算状态" width="200">
          <template slot-scope="scope">
            <div v-if="scope.row.settleState == 0">进行中</div>
            <div v-if="scope.row.settleState == 1">待结算</div>
            <div v-if="scope.row.settleState == 2">交易成功</div>
            <div v-if="scope.row.settleState == 3">交易关闭（交易失败）</div>
          </template>
          
        </el-table-column>
        <el-table-column prop="createTime" label="下单时间" width="200"></el-table-column>
        <el-table-column label="入账时间" width="200">
          <template slot-scope="scope">
            <div>{{scope.row.updateTime ? scope.row.updateTime : '-'}}</div>
          </template>
        </el-table-column>
        <el-table-column label="账户" width="200">
          <template slot-scope="scope">
            <div :data-id="scope.row.id">店铺余额</div>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="goToInfo(scope.row)">查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="flex-row-end" style="padding-top:20px;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5,10,15,20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getShopAssetRecord } from '@/api/assetsManage/account'
import { getShopAsset } from '@/api/assetsManage/verifyFund'
export default {
  data(){
    return{
      searchList:{
        pageSize:5,
        pageNum:1,
      },
      list:[],
      listTotal:0,
      shopAsset:''
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getData()
    this.getMoney()
  },
  methods:{
    getMoney(){
      getShopAsset(this.shop.shopId).then(res=>{
        console.log('默认数据',res)
        if(res.code == 200){
          this.shopAsset = res.data.shopAsset;
        }
      })
    },
    getData(){
      let startDate,endDate;
      if(this.$route.query.dateType == 'day'){
        startDate = this.$route.query.balanceDay + ' 00:00:00'
        endDate = this.$route.query.balanceDay + ' 23:59:59'
      }
      if(this.$route.query.dateType == 'month'){
        startDate = this.$route.query.startDate
        endDate = this.$route.query.endDate
      }
      this.searchList.startDate = startDate
      this.searchList.endDate = endDate
      console.log('全部请求的数据',this.searchList)
      getShopAssetRecord(this.searchList).then(res=>{
        console.log('全部交易记录列表',res)
        if(res.code ==200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
      })
    },
    reset(){
      this.searchList.amountType = ''
      this.searchList.businessNumber = ''
      this.searchList.wxNumber = ''
      this.searchList.tradeType = ''
      this.getData()
    },
    goToInfo(e){
      if(e.tradeType == 3){
        console.log('充值')
        this.$emit('changeActive','1');
        return;
      }
      if(e.tradeType == 25){
        console.log('提现')
        this.$router.push({
          path:'/assetsManage/account/cashoutDetail',
          query:{
            id:e.id
          }
        })
        return;
      }
      if(e.tradeType == 6){
        console.log('会员卡开通记录')
        this.$router.push({
          path:'/memberManage/memberCenter',
          query:{
            type:1
          }
        })
        return;
      }
      this.$router.push({
        path:'/order/orderList/orderinfo',
        query:{
          orderId:e.orderId
        }
      })
    },
    goToMybill(e){
      this.$router.push({
        path:'/assetsManage/myBill',
        query:{
          orderId:e.orderId,
          afterSaleId:e.afterSaleId
        }
      })
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>